<template>
<div>
    <div class="container-fluid">
        <topNav />
        <div class="body-content">
        <router-view></router-view>
        </div>
    </div>
    <loading v-show="$store.state.loading"></loading>
</div>
</template>

<script>
import {checkLog} from './api/account.js';
import {LOGIN,LOGOUT} from './store/mutation_types.js';
import loading from './components/common/loading.vue';
import topNav from './components/common/topNav.vue';
export default {
    created(){
        if(window.localStorage.userInfo){
            this.log();
        }
    },
    methods:{
        async log(){
            let res=await checkLog(JSON.parse(window.localStorage.userInfo).id);
            if(res.data.res!=1){
                this.$store.commit(LOGOUT);
            }else{
                this.$store.commit(LOGIN,JSON.parse(window.localStorage.userInfo),true);
            }
        }
    },
    components:{
        topNav,loading
    },
}
</script>

<style lang="less">
@import "./assets/css/theme.less";
body{
    background-color: #ececec;
}
.container-fluid{
    padding:0;
    margin: 0;
    background-color: #ececec;
    padding-bottom: 10px;
}
.body-content{
    height: 100%;
    width: 100%;
    overflow-x: hidden;
}
@media screen and (min-width:768px){
    .body-content{
        padding:71px 15px 15px 15px;
    }
}
@media screen and (max-width:768px){
    .body-content{
        padding:71px 2px 2px 2px;
    }
}
</style>



